let initialState = {
  users: [],
  user: {
    name: {
        title: '',
        first: '',
        last: ''
    },
    gender: 'Male',
    email: '',
    username: '',
    password: '',
    dob: '',
    phone: ''
  },
  isLogedIn: false,
  showDialog: false,
  isLoading: false
};

export default function loginReducer(state = initialState, action) {
  switch (action.type) {
    case 'USER_DATA_LOADING':
        return {
          ...state,
          isLoading: action.status,
        };
    case 'SHOW_DIALOG':
        return {
            ...state,
            showDialog: action.status,
        };
    case 'SET_USER_DATA':
        return {
          ...state,
          users: [...action.data],
        };
    case 'ADD_USER_DATA':
        return {
          ...state,
          users: [action.user, ...state.users],
        };
    default:
        return { ...state };
  }
}
